@import "../../../assets/styles/global";

.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(0, 0, 0, 0.69);
  z-index: 1;

  .container {
    width: 1366px;
    padding-top: 100px;
    display: flex;
    flex-direction: column;

    .nominations {
      padding-top: 50px;
      display: flex;
      justify-content: flex-start;

      .item {
        position: relative;

        .frame {
          width: 443px;

          img {
            width: 100%;
          }
        }

        .text {
          width: 370px;
          position: absolute;
          top: 65px;
          left: 48.5%;
          transform: translateX(-50%);

          .title {
            font-family: $Gotham;
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-align: center;
            text-transform: uppercase;
            color: #FFFFFF;
            text-shadow: 0 0 4px rgba(255, 255, 255, 0.78);
          }

          .description {
            padding-top: 32px;
            font-family: $Nunito;
            font-style: normal;
            font-weight: normal;
            font-size: 24px;
            line-height: 33px;
            text-align: center;
            color: #FFFFFF;
          }
        }

        &:nth-child(2) {
          margin: 140px 0 0 30px;
        }
      }
    }
  }
}

@media screen and (max-width: $Tablet) {
  .wrapper {

    .container {
      width: (1366px/$val);
      padding-top: (100px/$val);

      .nominations {
        padding-top: (50px/$val);

        .item {

          .frame {
            width: (443px/$val);
          }

          .text {
            width: (370px/$val);
            top: (65px/$val);

            .title {
              font-size: (24px/$val);
              line-height: (29px/$val);
              text-shadow: 0 0 (4px/$val) rgba(255, 255, 255, 0.78);
            }

            .description {
              padding-top: (32px/$val);
              font-size: (24px/$val);
              line-height: (33px/$val);
            }
          }

          &:nth-child(2) {
            margin: (140px/$val) 0 0 (30px/$val);
          }
        }
      }
    }
  }
}

