@import "../../../assets/styles/global";

.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(0, 0, 0, 0.69);
  z-index: 1;

  .container {
    width: 1366px;
    padding: 100px 0;
    display: flex;
    flex-direction: column;

    .content {
      position: relative;
      margin-top: 50px;

      .border {
        width: 1388px;

        img {
          width: 100%;
        }
      }

      .frameLeft {
        width: 590px;
        position: absolute;
        top: -22px;
        left: 0;

        img {
          width: 100%;
        }
      }

      .frameRight {
        width: 590px;
        position: absolute;
        top: -22px;
        right: 0;
        transform: scale(-1, 1);

        img {
          width: 100%;
        }
      }

      .title {
        font-family: $Nunito;
        font-style: normal;
        font-weight: 600;
        font-size: 64px;
        line-height: 87px;
        color: #FFFFFF;
      }

      .description {
        font-family: $Nunito;
        font-style: normal;
        font-weight: normal;
        font-size: 27px;
        line-height: 37px;
        text-align: center;
        color: #FFFFFF;
      }
    }

    .textBgd {
      .title {
        position: absolute;
        top: 15px;
        left: 100px;
      }

      .description {
        width: 500px;
        position: absolute;
        top: 180px;
        left: 100px;
      }
    }

    .textIgda {
      .title {
        position: absolute;
        top: 15px;
        right: 100px;
      }

      .description {
        width: 500px;
        position: absolute;
        top: 180px;
        right: 100px;
      }
    }
  }
}

@media screen and (max-width: $Tablet) {
  .wrapper {

    .container {
      width: (1366px/$val);
      padding: (100px/$val) 0;

      .content {
        margin-top: (50px/$val);

        .frameLeft {
          width: (590px/$val);
          top: (-22px/$val);
        }

        .frameRight {
          width: (590px/$val);
          top: (-22px/$val);
        }

        .border {
          width: (1388px/$val);
        }

        .title {
          font-size: (64px/$val);
          line-height: (87px/$val);
        }

        .description {
          font-size: (27px/$val);
          line-height: (37px/$val);
        }
      }

      .textBgd {
        .title {
          top: (15px/$val);
          left: (100px/$val);
        }

        .description {
          width: (500px/$val);
          top: (180px/$val);
          left: (100px/$val);
        }
      }

      .textIgda {
        .title {
          top: (15px/$val);
          right: (100px/$val);
        }

        .description {
          width: (500px/$val);
          top: (180px/$val);
          right: (100px/$val);
        }
      }
    }
  }
}

